.App {
  width: 1600px;
  height: 900px;
  margin: 0 auto;
  color: #fff;
  overflow: hidden;
}

.content {
  display:flex;
  flex-direction: row;
}

.sidebar {
  width: 250px;
  height: 900px;
  padding: 0 20px;
}

.logo {
  display: flex;
  align-items: center;
}

.sidebar-link{
  color: #fff;
  text-decoration: none;
  font-size: 25px;
}

.sidebar-link:hover{
  text-decoration: underline;
  color: #4682B4;
}

.link-a{
  color: #fff;
  text-decoration: none;
}

.sidebar-item {
  margin-top: 30px;
}

.link-a-first{
  margin-top: 300px;
}

.link-a-second{
  margin-top: 40px;
}

.link-a:hover{
  text-decoration:underline;
  color: #4682B4;
}

.router{
  width: 1350px;
  height: 900px;
  overflow-y: scroll;
}
